* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    font-size: 62.5%;
    height: 100vh;
    width: 100vw;
    background-color: black;
}
*:focus {
    outline: none;
    box-shadow: 0 0 0 0.2rem red;
    transform: scale(1.1);
    transition: all 0.2s ease 0s;
}
